const FooterWrapper = styled(Box)(({ theme }) => ({
	// marginTop: theme.spacing(6),
	borderTop: `1px solid ${theme.palette.common.boxBorder}`,
	backgroundColor: "rgb(35, 19, 50)",
	[theme.breakpoints.down('md')]: {
		marginTop: theme.spacing(3),
	}
}))

const FooterBar = styled(Box)(({ theme }) => ({
	backgroundColor: theme.palette.common.footerBg,
}))
const FooterContent = styled(Box)(({ theme }) => ({
	display: 'flex',
	alignItems: "start",
	justifyContent: 'space-between',
	padding: `${theme.spacing(2)} 0`,
	gap: theme.spacing(3),
	minHeight: '80px',
}))

const FooterMain = styled(Box)(({ theme }) => ({
	display: 'flex',
	alignItems: 'center',
	justifyContent: 'space-between',
	padding: `${theme.spacing(2)} 0`,
	gap: theme.spacing(3),
	minHeight: '80px',
}))

const LogoWrapper = styled("a")(({ theme }) => ({
	height: '35px',
	display: 'flex',
	alignItems: 'center',
	cursor: 'pointer',

	'& img': {
		height: '100%',
		width: 'auto',
	},

	[theme.breakpoints.down('md')]: {
		height: '30px',
	}
}))

const LinksWrapper = styled(Box)(({ theme }) => ({
	display: 'flex',
	alignItems: 'center',
	gap: theme.spacing(4),

	'a': {
		textDecoration: 'none',
	},

	p: {
		textDecoration: 'none',
		color: theme.palette.common.secondTypography,
		transition: 'color 0.3s ease',

		'&:hover': {
			color: theme.palette.common.firstTypography,
		}
	},

	[theme.breakpoints.down('md')]: {
		gap: theme.spacing(2),
		flexDirection: 'column',
		justifyContent: 'center',
		textAlign: 'center',
	}
}))

const Linkitem = styled(Box)(() => ({
	color: "white",
	borderBottom: "0.5px solid rgba(168, 168, 168, 0.77)",
	paddingTop: "12px"
}))

export { Footer }