import React from 'react';
import { styled } from "@mui/material/styles";
import { Box, Button, Container, Grid } from "@mui/material";
import { liveChatImg, telegramImg, fanPageImg, csh247Img } from '../../assets/image';
import { SectionTitle } from '../../components/SectionTitle';



const Contacts = () => {
    return (
        <ContactWrapper>
            <Container maxWidth="xl">
                <SectionTitle>
                    Tng hp knh lin h chm sc khch hng Vip52
                </SectionTitle>
                <ContextWrapper>
                    Khi tham gia tri nghim ti cng game Vip52, ngi chi c th gp phi mt s vn  nh li h thng, s c giao dch thanh ton hoc cc trc trc khc trong qu trnh chi. Tuy nhin, ng lo lng, v i ng chm sc khch hng ca Vip52 lun sn sng h tr bn. Hy tham kho ngay cc hnh thc lin h di y nh!
                </ContextWrapper>
                <Grid container spacing={2} justifyContent="center" alignItems="center" style={{ padding: "30px 0px" }}>
                    <Grid item xs={6} sm={6} md={3}>
                        <ContactItem>
                            <ContactIcon src={liveChatImg} />
                            <LinkButton>Lin h ngay</LinkButton>
                        </ContactItem>
                    </Grid>
                    <Grid item xs={6} sm={6} md={3}>
                        <ContactItem>
                            <ContactIcon src={telegramImg} />
                            <LinkButton>Lin h ngay</LinkButton>
                        </ContactItem>
                    </Grid>
                    <Grid item xs={6} sm={6} md={3}>
                        <ContactItem>
                            <ContactIcon src={fanPageImg} />
                            <LinkButton>Lin h ngay</LinkButton>
                        </ContactItem>
                    </Grid>
                    <Grid item xs={6} sm={6} md={3}>
                        <ContactItem>
                            <ContactIcon src={csh247Img} />
                            <LinkButton>Lin h ngay</LinkButton>
                        </ContactItem>
                    </Grid>
                </Grid>
            </Container>
        </ContactWrapper>
    );
};

const ContactWrapper = styled(Box)(({ theme }) => ({
    paddingTop: "40px"
}))

const ContactIcon = styled('img')(({ theme }) => ({
    color: '#fff',
    textTransform: 'none',
    width: '100%', // Make button full width in the grid  
}));

const ContextWrapper = styled(Box)(() => ({
    color: "white",
    padding: "30px 10px"
}))


const ContactItem = styled(Box)(({ theme }) => ({
    display: "flex",
    flexDirection: "column",
    width: "100%",
    justifyContent: "center",
    alignItems: "center",
    padding: "10px",
    boxShadow: '0px 4px 20px rgba(0, 0, 0, 0.3)',
}))

const LinkButton = styled(Box)(({ theme }) => ({
    borderRadius: "10px",
    border: "2px solid #7a9c59",
    color: "#fff",
    transition: "all 0.3s",
    padding: "6px 25px",
    marginTop: "10px",
    fontSize: "17px",
    userSelect: "none",
    cursor: "pointer",
    '&:hover': {
        backgroundColor: "#7a9c59",
    }
}))

export { Contacts };