const TopGameWrapper = styled(Box)(({ theme }) => ({
    paddingTop: '30px',
    width: '100%',
    height: '100%',
}));

const GameItem = styled(Box)(({ theme }) => ({
    display: "flex",
    flexDirection: "column",
    width: "100%",
    justifyContent: "center",
    alignItems: "center",
    position: "relative",
    userSelect: "none",
    cursor: "pointer"
}))

const ContextWrapper = styled(Typography)(() => ({
    color: "white",
    padding: "8px",
    fontSize: "14px",
    textAlign: "center"
}))

const LinkButton = styled(Box)(({ theme }) => ({
    border: "2px solid #7a9c59",
    color: "#fff",
    transition: "all 0.3s",
    padding: "4px 15px",
    userSelect: "none",
    cursor: "pointer",
    '&:hover': {
        backgroundColor: "#7a9c59",
    }
}))

const PostDate = styled(Box)(() => ({
    width: "40px",
    // height: "30px",
    aspectRatio: "1/1",
    border: "2px solid #446084",
    backgroundColor: "white",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    flexDirection: "column",
    position: "absolute",
    color: "#446084",
    left: "-10px",
    top: '10px',
    "&:hover": {
        backgroundColor: "#446084",
        color: "white"
    }
}))
export { BettingGuide };